/**
 * Export Utilities for Customer Data
 * Provides PDF and CSV export functionality
 */

/**
 * Export customers to CSV
 * @param {Array} customers - Array of customer objects
 * @param {string} filename - Name of the file (without extension)
 */
export const exportToCSV = (customers, filename = 'customers') => {
  if (!customers || customers.length === 0) {
    alert('No customers to export');
    return;
  }

  // Define CSV headers
  const headers = [
    'Customer ID',
    'Name',
    'Email',
    'Phone',
    'State',
    'City',
    'Pincode',
    'Address',
    'Saving Plan',
    'Parent Promoter',
    'Status',
    'Created At'
  ];

  // Convert customers to CSV rows
  const rows = customers.map(customer => {
    const emailValue = customer?.email;
    const phoneValue = customer?.phone || customer?.mobile || '';
    const promoterName = customer?.promoter_name || 'Unassigned';
    
    return [
      customer?.customer_id || '',
      customer?.name || '',
      (emailValue && String(emailValue).trim() !== '') ? String(emailValue) : '',
      (phoneValue !== null && phoneValue !== undefined && String(phoneValue).trim() !== '') ? String(phoneValue) : '',
      customer?.state || '',
      customer?.city || '',
      customer?.pincode || '',
      customer?.address || '',
      customer?.saving_plan || 'â‚¹1000 per month for 20 months',
      promoterName,
      customer?.status || 'active',
      customer?.created_at ? new Date(customer.created_at).toLocaleDateString() : ''
    ];
  });

  // Combine headers and rows
  const csvContent = [
    headers.join(','),
    ...rows.map(row => 
      row.map(cell => {
        // Escape commas and quotes in cell values
        const cellStr = String(cell || '');
        if (cellStr.includes(',') || cellStr.includes('"') || cellStr.includes('\n')) {
          return `"${cellStr.replace(/"/g, '""')}"`;
        }
        return cellStr;
      }).join(',')
    )
  ].join('\n');

  // Create blob and download
  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
  const link = document.createElement('a');
  const url = URL.createObjectURL(blob);
  
  link.setAttribute('href', url);
  link.setAttribute('download', `${filename}_${new Date().toISOString().split('T')[0]}.csv`);
  link.style.visibility = 'hidden';
  
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  
  URL.revokeObjectURL(url);
};

/**
 * Export customers to PDF using jsPDF
 * @param {Array} customers - Array of customer objects
 * @param {string} filename - Name of the file (without extension)
 */
export const exportToPDF = async (customers, filename = 'customers') => {
  if (!customers || customers.length === 0) {
    alert('No customers to export');
    return;
  }

  try {
    // Dynamically import jsPDF (lazy load)
    const { default: jsPDF } = await import('jspdf');
    const doc = new jsPDF();

    // Set font
    doc.setFont('helvetica');

    // Title
    doc.setFontSize(18);
    doc.text('Customer Details Report', 14, 20);
    
    // Date
    doc.setFontSize(10);
    doc.text(`Generated on: ${new Date().toLocaleDateString()}`, 14, 28);
    doc.text(`Total Customers: ${customers.length}`, 14, 34);

    // Table settings
    const startY = 42;
    const pageHeight = doc.internal.pageSize.height;
    const margin = 14;
    const rowHeight = 7;
    let currentY = startY;
    const maxWidth = doc.internal.pageSize.width - (margin * 2);

    // Table headers
    const headers = [
      { header: 'ID', width: 20 },
      { header: 'Name', width: 35 },
      { header: 'Email', width: 40 },
      { header: 'Phone', width: 30 },
      { header: 'City', width: 25 },
      { header: 'Status', width: 20 }
    ];

    // Draw header row
    doc.setFillColor(70, 70, 70);
    doc.rect(margin, currentY - 5, maxWidth, rowHeight, 'F');
    doc.setTextColor(255, 255, 255);
    doc.setFontSize(9);
    doc.setFont('helvetica', 'bold');
    
    let xPos = margin;
    headers.forEach((col, index) => {
      doc.text(col.header, xPos + 2, currentY);
      xPos += col.width;
    });

    // Reset text color
    doc.setTextColor(0, 0, 0);
    doc.setFont('helvetica', 'normal');
    currentY += rowHeight;

    // Customer rows
    customers.forEach((customer, index) => {
      // Check if we need a new page
      if (currentY + rowHeight > pageHeight - 20) {
        doc.addPage();
        currentY = margin;
        
        // Redraw header on new page
        doc.setFillColor(70, 70, 70);
        doc.rect(margin, currentY - 5, maxWidth, rowHeight, 'F');
        doc.setTextColor(255, 255, 255);
        doc.setFont('helvetica', 'bold');
        xPos = margin;
        headers.forEach((col) => {
          doc.text(col.header, xPos + 2, currentY);
          xPos += col.width;
        });
        doc.setTextColor(0, 0, 0);
        doc.setFont('helvetica', 'normal');
        currentY += rowHeight;
      }

      // Alternate row colors
      if (index % 2 === 0) {
        doc.setFillColor(245, 245, 245);
        doc.rect(margin, currentY - 5, maxWidth, rowHeight, 'F');
      }

      // Customer data
      const emailValue = customer?.email;
      const phoneValue = customer?.phone || customer?.mobile || '';
      const customerEmail = (emailValue && String(emailValue).trim() !== '') ? String(emailValue) : 'N/A';
      const customerPhone = (phoneValue !== null && phoneValue !== undefined && String(phoneValue).trim() !== '') ? String(phoneValue) : 'N/A';

      doc.setFontSize(8);
      xPos = margin;
      
      // Truncate long text
      const truncate = (text, maxLen) => {
        const str = String(text || '');
        return str.length > maxLen ? str.substring(0, maxLen - 3) + '...' : str;
      };

      doc.text(truncate(customer?.customer_id || '', 8), xPos + 2, currentY);
      xPos += headers[0].width;
      
      doc.text(truncate(customer?.name || '', 20), xPos + 2, currentY);
      xPos += headers[1].width;
      
      doc.text(truncate(customerEmail, 25), xPos + 2, currentY);
      xPos += headers[2].width;
      
      doc.text(truncate(customerPhone, 15), xPos + 2, currentY);
      xPos += headers[3].width;
      
      doc.text(truncate(customer?.city || '', 15), xPos + 2, currentY);
      xPos += headers[4].width;
      
      doc.text(customer?.status || 'active', xPos + 2, currentY);

      currentY += rowHeight;
    });

    // Save PDF
    doc.save(`${filename}_${new Date().toISOString().split('T')[0]}.pdf`);
  } catch (error) {
    console.error('Error generating PDF:', error);
    alert('Failed to generate PDF. Please make sure jsPDF is installed.');
  }
};

